import {
  Component, OnDestroy,
  OnInit, ViewChild,
} from '@angular/core';
import { Customer } from '../shared/customer.model';
import { CustomersService } from '../shared/customers.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import 'rxjs/add/operator/switchMap';
import { MessageService } from '../../shared/message/message.service';
import { AppService } from '../../app.service';
import {UnsavedChangesModalComponent} from '../../shared/modal/unsaved-changes-modal.component';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {NgForm} from '@angular/forms';

@Component({
  selector: 'customer',
  styleUrls: ['./customer.component.css'],
  templateUrl: './customer.component.html'
})
export class CustomerComponent implements OnInit, OnDestroy {

  public stateAbbreviations = [
    'AL',
    'AK',
    'AZ',
    'AR',
    'CA',
    'CO',
    'CT',
    'DE',
    'FL',
    'GA',
    'HI',
    'ID',
    'IL',
    'IN',
    'IA',
    'KS',
    'KY',
    'LA',
    'ME',
    'MD',
    'MA',
    'MI',
    'MN',
    'MS',
    'MO',
    'MT',
    'NE',
    'NV',
    'NH',
    'NJ',
    'NM',
    'NY',
    'NC',
    'ND',
    'OH',
    'OK',
    'OR',
    'PA',
    'RI',
    'SC',
    'SD',
    'TN',
    'TX',
    'UT',
    'VT',
    'VA',
    'WA',
    'WV',
    'WI',
    'WY',
    'AS',
    'DC',
    'FM',
    'GU',
    'MH',
    'MP',
    'PW',
    'PR',
    'VI',
    'AE',
    'AA',
    'AE',
    'AP'
  ];
  public model = new Customer();
  public title = 'Create';
  public message = { type: '', style: '', text: '' };

  private emptyGuid = '00000000-0000-0000-0000-000000000000';
  private submittedSuccessfully = false;

  @ViewChild('customerForm') private customerForm: NgForm;

  constructor(
    private customersService: CustomersService,
    private router: Router,
    private route: ActivatedRoute,
    private messageService: MessageService,
    private modalService: NgbModal,
    private appService: AppService) {
    this.model.customerId = this.emptyGuid;
    this.model.isSystemAdmin = false;
  }

  public ngOnInit() {
    let id = '';
    this.route.params
      .subscribe((params) => id = params['id']);
    this.appService.logDebug('Load Customer ID:', id);
    if (id !== undefined && id !== '') {
      this.title = 'Edit';
      this.customersService
        .getCustomer(id)
        .subscribe((customer: Customer) => this.model = customer,
        (err) => this.handleFailure(err));
    }
  }

  canDeactivate(): Promise<boolean> | boolean {
    if (this.customerForm.dirty && !this.submittedSuccessfully) {
      const result = this.modalService.open(UnsavedChangesModalComponent).result;

      return result.then(
        () => { return true; },
        () => { return false; }
      );
    } else {
      return true;
    }
  }

  public onSubmit() {
    let request;

    if (this.model.customerId === this.emptyGuid) {
      request = this.customersService.postCustomer(this.model);
    } else {
      request = this.customersService.putCustomer(this.model.customerId, this.model);
    }

    request.subscribe(
      (customer) => this.handleSuccess(customer),
      (err) => this.handleFailure(err)
    );
  }

  public ngOnDestroy() {
    this.messageService.clearFailure();
  }

  private handleFailure(err) {
    this.submittedSuccessfully = false;
    this.messageService.fail({ text: err.message });
  }

  private handleSuccess(customer) {
    this.submittedSuccessfully = true;
    this.customersService.setUpdatedCustomer(customer);
    this.messageService.success({ text: 'Saved!', autoHide: true });
    this.router.navigate(['/customers']);
  }
}



