import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { AppConfig } from 'app/shared/config/app.config.model';
import { Observable } from 'rxjs/Observable';
import { Exception } from '../exceptions/exception.model';

@Injectable()
export class AppConfigService {
    private config: AppConfig;
    private configFileUrl = '/portal/assets/config/app.config.json';

    constructor(private http: Http) {
    }

    public load(baseUrl?: string): Promise<AppConfig> {
        baseUrl = baseUrl || this.configFileUrl;
         return this.http.get(baseUrl)
            .map((res) => res.json())
            .toPromise()
            .then(data => {
                this.config = data;
                return data;
            })
            .catch((error: any) => Observable.throw(this.handleException(error)));
    }

    public get(key: any) {
        return this.config[key];
    }

    private handleException(error) {
        console.error('ERROR LOADING APP CONFIGURATION', error);
        const ex = new Exception(0, '');

        ex.id = error.status || 500;

        if (error._body !== '') {
            const responseBody = JSON.parse(error._body);
            ex.message = responseBody.message;
        } else {
            ex.message = error.statusText || 'Server Error';
        }

        return ex;
    }
}



