import {
  Component,
  ViewEncapsulation,
  ViewChild,
  OnInit,
  OnDestroy
} from '@angular/core';
import { AppService } from './app.service';
import { AppConfigService } from './shared/config/app.config.service';
import { AuthModule } from './shared/auth/oidc/modules/auth.module';
import { OIDCUser } from './shared/auth/oidc/oidc.user.model';
import { UsersService } from 'app/+users/shared/users.service';
import { PermissionsService } from './shared/security/permissions.service';
import { CustomersService } from './+customers/shared/customers.service';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import { Subscription } from 'rxjs/Subscription';
import { Affiliation } from './+affiliations/shared/affiliation.model';
import { PageLoadingComponent } from './shared/page-load/page-load.component';
import { Exception } from './shared/exceptions/exception.model';
import { AUTHERROR } from './shared/auth/oidc/services/oidc.security.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  encapsulation: ViewEncapsulation.None,
  // DO NOT REMOVE THESE STYLEURLS
  // REMOVING THESE STYLE URLS WILL BREAK STYLES ONCE DEPLOYED BUT NOT BE EVIDENT TO YOU IN LOCAL
  styleUrls: ['../assets/css/main.css', '../assets/sass/app-style-overrides.scss',
    '../../node_modules/ng-bootstrap-to-bootstrap-3/dist/ng-bootstrap-to-bootstrap-3.min.css']
})

export class AppComponent implements OnInit, OnDestroy {

  @ViewChild(PageLoadingComponent) pageLoader: PageLoadingComponent;

  userAuthSubscription: Subscription;
  userLoadedObservable: Observable<Affiliation>;

  public constructor(
    public appService: AppService,
    private permissionsService: PermissionsService,
    private usersService: UsersService,
    private customersService: CustomersService,
    private appConfig: AppConfigService
  ) {
    this.appService.initOIDC();
  }

  public ngOnInit() {
    this.userLoadedObservable = this.appService.getAuthenticatedUser();
    this.pageLoader.show();
    this.pageLoader.setMessage('Loading...');
    const isAuth: any = this.appService.getIsAuthorized() || false;
    if (this.getLocationHash()) {
        this.appService.processLogin().subscribe((next: OIDCUser) => {
          this.appService.blockAuthCalls(true);
          this.userAuthSubscription = this.registerUserObserver(this.pageLoader, this.usersService, this.customersService, this.appService, this.permissionsService);
        }, (error: Exception) => {
          this.pageLoader.setMessage('Trying to log you in');

          this.appService.blockAuthCalls(false);
          // Error authenticating, reattempt authorization
          if (error.id === AUTHERROR.TOKEN_STATE_ERROR) {
            this.callAuthorize();
          } else {
            this.logoff();
          }
        }, () => {
          this.pageLoader.hide();
        });

    } else {
      if (!isAuth) {
          this.callAuthorize();
      } else {
        this.userAuthSubscription = this.registerUserObserver(this.pageLoader, this.usersService, this.customersService, this.appService, this.permissionsService);
      }
    }
  }

  public ngOnDestroy(): void {
    if (this.userAuthSubscription) {
      this.userAuthSubscription.unsubscribe();
    }
  }

  registerUserObserver(loader: PageLoadingComponent, userService: UsersService, custService: CustomersService, appService: AppService, permissions: PermissionsService): Subscription {
    return userService.getCurrentUser().subscribe(authenticatedUser => {
      custService.setCurrentCustomer(authenticatedUser.customer);
      appService.setAuthenticatedUser(authenticatedUser);
      permissions.setPermissions(authenticatedUser.securityRole.level);
      loader.hide();
      this.appService.blockAuthCalls(false);
      // Listen for changes to storage where the user has been logged out in another tab or user cleared localstorage
      window.addEventListener('storage', (event) => {
          this.storageEventHandler(event);
      });
      }, (error) => {
          /* 401 occurs when authorization on ID server is valid but portal API is not*/
          if (error.status && error.status === 401 || error.status === '401') {
            this.callAuthorize();
          } else {
            /* any other condition -- log user out */
            this.logoff();
          }
      });
  }

  storageEventHandler(event: StorageEvent): void {
    if ((event.storageArea.length === 0) ||
      (event.key && event.key === '_isAuthorized' && event.newValue.match(/(\"{2,4})|(\'{2,4})|(false)/g))) {
      this.logoff();
      return;
    }
  }

  callAuthorize() {
    this.pageLoader.setMessage('Redirecting you to sign in');
    this.appService.redirectToAuthorization();
  }

  public logoff() {
    this.appService.logoff();
  }

  public getLocationHash(): string {
    return window.location.hash;
  }

}



