import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CustomerComponent } from './+customer/customer.component';
import { CustomerListComponent } from './+customer-list/customer-list.component';
import {UnsavedChangesModalComponent} from '../shared/modal/unsaved-changes-modal.component';
import { CUSTOMER_ROUTES } from './customer.routes';
import {CustomersService} from './shared/customers.service';
import { PortalFormsModule } from '../shared/forms/portal-forms.module';

@NgModule({
  declarations: [
    CustomerComponent,
    CustomerListComponent
  ],
  imports: [
    PortalFormsModule,
    RouterModule.forChild(CUSTOMER_ROUTES)
  ],
  exports: [
    CustomerComponent,
    CustomerListComponent
  ],
  providers: [
    CustomersService
  ],
  entryComponents: [UnsavedChangesModalComponent]
})
export class CustomerModule {}



