import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import * as _ from 'lodash';

import { UserSearchResult } from '../../+users/shared/user-search-result.model';
import { UsersService } from '../shared/users.service';

import { Customer } from '../../+customers/shared/customer.model';
import { CustomersService } from '../../+customers/shared/customers.service';
import { Permissions } from '../../shared/security/permissions.model';
import { PermissionsService } from '../../shared/security/permissions.service';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'user-list',
  styleUrls: ['./user-list.component.css'],
  templateUrl: './user-list.component.html'
})
export class UserListComponent implements OnInit, OnDestroy {
  public currentCustomer: Customer = new Customer();
  public userSearchResults: UserSearchResult[] = [];
  public customers: Customer[];
  public message = {type: '', style: '', text: ''};
  public canIUse: Permissions = new Permissions(false, false, false, false);
  public fetchingUsers = true;
  public updatedUser: UserSearchResult = new UserSearchResult();

  private observableSubscription;

  constructor(private route: ActivatedRoute,
              private usersService: UsersService,
              private permissionsService: PermissionsService,
              private customerService: CustomersService) {
  }

  public ngOnInit() {
    const currentCustomerObservable = this.customerService.getCurrentCustomer();
    const routeDataObservable = this.route.data;
    const updatedUserObservable = this.usersService.getUpdatedUser().first();

    this.observableSubscription = Observable
      .combineLatest(currentCustomerObservable, routeDataObservable, updatedUserObservable)
      .subscribe(data => {
        const [currentCustomer, customerData, updatedUser] = data;
        const currentCustomerExists = currentCustomer && currentCustomer.name;

        this.currentCustomer = currentCustomer;
        this.customers = customerData.customers;
        this.updatedUser = updatedUser;

        if (currentCustomerExists) {
          this.initialize(this.currentCustomer);
        } else {
          this.initialize(customerData.customers[0]);
        }
      });

    this.permissionsService.getPermissions().subscribe(featureAvailability => {
      this.canIUse = featureAvailability;
    });

  }

  public ngOnDestroy() {
    if (this.observableSubscription) {
      this.observableSubscription.unsubscribe();
    }
  }

  public onCustomerChange(customerId) {
    this.userSearchResults = [];

    if (customerId) {
      if (!this.currentCustomer || !this.currentCustomer.customerId || customerId !== this.currentCustomer.customerId) {
        this.customerService.setCurrentCustomer(_.find(this.customers, {customerId}));
      }
      this.getUsersForCustomerId(customerId);
    }
  }

  private initialize(customer) {
    if (customer !== undefined && customer.customerId) {
      this.getUsersForCustomerId(customer.customerId);

      if (!this.currentCustomer.customerId) {
        this.customerService.setCurrentCustomer(customer);
      }
    }
  }

  private getUsersForCustomerId(customerId: string) {
    this.fetchingUsers = true;
    this.usersService.getUsers(customerId).subscribe((results: UserSearchResult[] = []) => {
        results = results && results.length ? results : [];

        if (this.updatedUser && this.updatedUser.userId) {
          const index = _.findIndex(results, {userId: this.updatedUser.userId});

          if (index >= 0) {
            results.splice(index, 1, this.updatedUser);
          } else {
            results.push(this.updatedUser);
          }

          // Clear the updatedUser to prevent overwriting newer server data
          this.usersService.setUpdatedUser(null);
        }

        this.fetchingUsers = false;
        this.userSearchResults = results || [];
      },
      (err) => this.handleFailure(err));
  }

  private handleFailure(err) {
    this.createMessage('fail', err.message);
  }

  private createMessage(type, text) {
    this.message.type = type;
    this.message.text = text;

    switch (type) {
      case 'pass':
        this.message.style = 'success';
        break;
      default:
        this.message.style = 'danger';
    }
  }
}



