import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { HttpModule, Http } from '@angular/http';
import {
  NgModule,
  ApplicationRef,
  APP_INITIALIZER
} from '@angular/core';
import {
  RouterModule,
  PreloadAllModules
} from '@angular/router';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
/*
 * Platform and Environment providers/directives/pipes
 */
// import { ENV_PROVIDERS } from './environment';
import { APP_ROUTES } from './app.routes';
// App is our top level component
import { AppComponent } from './app.component';
import { APP_RESOLVER_PROVIDERS } from './app.resolver';
import { AppService } from './app.service';
import { NoContentComponent } from './no-content';
import { NoWhitespaceDirective } from './shared/forms/validators/no-whitespace.directive';

// Core providers
import { LayoutModule } from './shared/layout/layout.module';
import { RequestService } from './shared/auth/request.service';
import { ExceptionService } from './shared/exceptions/exception.service';
import { AuthGuardService } from './shared/auth/auth.guard.service';

// Authorization providers
import { AuthModule } from './shared/auth/oidc/modules/auth.module';
import { OidcSecurityService } from './shared/auth/oidc/services/oidc.security.service';
import { OpenIDImplicitFlowConfiguration, DefaultConfiguration } from './shared/auth/oidc/modules/auth.configuration';

import { MessageService } from './shared/message/message.service';
import { SecurityRolesService } from './shared/security/security-role.service';
// Main style entry point
// import '../styles.css';
import { UserModule } from './+users/users.module';
import { CustomerModule } from './+customers/customers.module';
import { DashboardModule } from './dashboard/dashboard.module';
import { CanDeactivateGuardService } from './shared/routing/can-deactivate-guard.service';
import { CustomersService } from './+customers/shared/customers.service';
import { AppConfigService } from './shared/config/app.config.service';
import { PermissionsService } from './shared/security/permissions.service';
import { UnauthorizedComponent } from './error-pages/unauthorized/unauthorized.component';
import { PageLoadingModule } from './shared/page-load/page-load.module';

// Application wide providers
const APP_PROVIDERS = [
  ...APP_RESOLVER_PROVIDERS,
  AppService
];

export function initAppConfig(appConfigSvc: AppConfigService) {
  return () => appConfigSvc.load();
}

/**
 * `AppModule` is the main entry point into Angular2's bootstraping process
 */
@NgModule({
  bootstrap: [AppComponent],
  declarations: [
    AppComponent,
    NoContentComponent,
    UnauthorizedComponent
  ],
  imports: [ // import Angular's modules
    DashboardModule,
    BrowserModule,
    FormsModule,
    HttpModule,
    LayoutModule,
    CustomerModule,
    UserModule,
    PageLoadingModule,
    NgbModule.forRoot(),
    AuthModule.forRoot(),
    RouterModule.forRoot(APP_ROUTES /*, { useHash: false, preloadingStrategy: PreloadAllModules }*/)
  ],
  providers: [ // expose our Services and Providers into Angular's dependency injection
    // ENV_PROVIDERS,
    AppConfigService,
    {
      provide: APP_INITIALIZER,
      useFactory: initAppConfig,
      deps: [AppConfigService],
      multi: true
    },
    OidcSecurityService,
    DefaultConfiguration,
    APP_PROVIDERS,
    RequestService,
    AuthGuardService,
    MessageService,
    SecurityRolesService,
    CanDeactivateGuardService,
    PermissionsService,
    ExceptionService
  ]
})
export class AppModule {

  constructor(
    public appRef: ApplicationRef,
    public appState: AppService) { }
}



