import { Component, OnInit } from '@angular/core';
import { OidcSecurityService } from '../../shared/auth/oidc/services/oidc.security.service';

@Component({
  selector: 'app-unauthorized',
  template: `<h1>Unauthorized</h1>
            <div class='container'>
              <div>
                <p>We're having trouble logging you in. Would you like to try logging in again.</p>
              </div>
              <div><button (click)='logOff()'>Sign In</button></div>
            </div>`
})
export class UnauthorizedComponent {

  constructor(private oidcSecurityService: OidcSecurityService) { }

  public logOff() {
    this.oidcSecurityService.logoff();
  }

}



