import {Affiliation} from '../../+affiliations/shared/affiliation.model';
import {SecurityRoles} from './security-role.model';
import { Permissions } from './permissions.model';
import {Observable} from 'rxjs/Observable';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

/** Permissions business logic                                     */
/** SystemAdmin, SupportAdmin can manage customers, manage users   */
/** CustomerAdmin can manage users                                 */
/** User == dashboard only                                         */

export class PermissionsService {

  private permissions: BehaviorSubject<Permissions> = new BehaviorSubject(new Permissions(false, false, false, false));

  private userAccessLevel = 1000;

  public getPermissions(): Observable<Permissions> {
    return this.permissions.share();
  }

  public setPermissions(userAccessLevel) {
    this.userAccessLevel = userAccessLevel;

    const manageCustomers = this.userAccessLevel <= SecurityRoles.SupportAdmin ? true : false;
    const reports = this.userAccessLevel <= SecurityRoles.SupportAdmin ? true : false;
    const manageUsers = this.userAccessLevel <= SecurityRoles.CustomerAdmin ? true : false;
    const dashboard = this.userAccessLevel <= SecurityRoles.User ? true : false;

    const permissions = new Permissions(
      manageCustomers,
      manageUsers,
      reports,
      dashboard
    );
    this.permissions.next(permissions);
  }

}



