import { Injectable } from '@angular/core';
import { RequestService } from '../../shared/auth/request.service';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import { Customer } from './customer.model';
import { Exception } from '../../shared/exceptions/exception.model';
import { ExceptionService } from '../../shared/exceptions/exception.service';
import {ActivatedRouteSnapshot, Resolve, RouterStateSnapshot} from '@angular/router';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

@Injectable()
export class CustomersService implements Resolve<Customer> {
    private currentCustomer: BehaviorSubject<Customer> = new BehaviorSubject(new Customer());
    private updatedCustomer: BehaviorSubject<Customer> = new BehaviorSubject(new Customer());
    private baseUrl = `/portal/api`;

    constructor(private requestService: RequestService, private exceptionService: ExceptionService) {
    }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Customer[]> {
      return this.getCustomers();
    }

    public getCustomers(): Observable<Customer[]> {
        return this.requestService.authGet(`${this.baseUrl}/v1/customers`)
            .map((res) => res.json())
            .catch((error: any) => Observable.throw(this.exceptionService.handleException(error)));
    }

    public getCustomer(id: string): Observable<Customer> {
        return this.requestService.authGet(`${this.baseUrl}/v1/customers/${id}`)
            .map((res) => res.json())
            .catch((error: any) => Observable.throw(this.exceptionService.handleException(error)));
    }

    public getCurrentCustomer(): Observable<Customer> {
      return this.currentCustomer.share();
    }

    public setCurrentCustomer(customer: Customer) {
      this.currentCustomer.next(customer);
    }

    public getUpdatedCustomer(): Observable<Customer> {
      return this.updatedCustomer.share();
    }

    public setUpdatedCustomer(customer: Customer) {
      this.updatedCustomer.next(customer);
    }

    public postCustomer(model: Customer): Observable<Customer> {
        return this.requestService.authPost(`${this.baseUrl}/v1/customers`, model)
            .map((res) => res.json())
            .catch((error: any) => Observable.throw(this.exceptionService.handleException(error)));
    }

    public putCustomer(id: string, model: Customer): Observable<Customer> {
        return this.requestService.authPut(`${this.baseUrl}/v1/customers/` + id, model)
            .map((res) => res.json())
            .catch((error: any) => Observable.throw(this.exceptionService.handleException(error)));
    }
}



