import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { SecurityRole, SecurityRoles } from './security-role.model';
import { RequestService } from '../../shared/auth/request.service';
import { Exception } from '../../shared/exceptions/exception.model';
import { RequestOptions, Headers } from '@angular/http';
import { ExceptionService } from '../../shared/exceptions/exception.service';

@Injectable()
export class SecurityRolesService {
    private baseUrl = `/portal/api`;

    constructor(private requestService: RequestService, private exceptionService: ExceptionService) {
    }

    public getRoles(customerId: string): Observable<SecurityRole[]> {
        const headers = new Headers();
        headers.append('customerId', customerId);
        const requestOptions = new RequestOptions({
            headers: headers
        });

        return this.requestService.authGet(`${this.baseUrl}/v1/security-roles`, requestOptions)
            .map((res) => res.json())
            .catch((error: any) => Observable.throw(this.exceptionService.handleException(error)));
    }
}



