import {
  Component, OnDestroy,
  OnInit
} from '@angular/core';
import { Customer } from '../shared/customer.model';
import {
  ActivatedRoute
} from '@angular/router';
import {CustomersService} from '../shared/customers.service';
import { Observable } from 'rxjs/Observable';
import * as _ from 'lodash';

@Component({
  selector: 'customer-list',
  styleUrls: ['./customer-list.component.css'],
  templateUrl: './customer-list.component.html'
})

export class CustomerListComponent implements OnInit, OnDestroy {
  public customers: Customer[] = [];
  public message = { type: '', style: '', text: '' };

  private observableSubscription;

  constructor(
    private customersService: CustomersService,
    private route: ActivatedRoute) {
  }

  public ngOnInit() {
    const routeObservable = this.route.data;
    const updatedCustomerObservable = this.customersService.getUpdatedCustomer();

    this.observableSubscription = Observable
      .combineLatest(routeObservable, updatedCustomerObservable)
      .subscribe(data => {
        const [customerData, updatedCustomer] = data;
        const customers = customerData.customers;

        if (updatedCustomer && updatedCustomer.customerId) {
          const customerIndex = _.findIndex(customers, {customerId: updatedCustomer.customerId});

          if (customerIndex >= 0) {
            customers.splice(customerIndex, 1, updatedCustomer);
          } else {
            customers.push(updatedCustomer);
          }

          // Clear the updatedCustomer to prevent overwriting newer server data
          this.customersService.setUpdatedCustomer(null);
        }

        this.customers = customers;
      });
  }

  public ngOnDestroy() {
    if (this.observableSubscription) {
      this.observableSubscription.unsubscribe();
    }
  }
  private handleFailure(err) {
    this.createMessage('fail', err.message);
  }

  private createMessage(type, text) {
    this.message.type = type;
    this.message.text = text;

    switch (type) {
      case 'pass':
        this.message.style = 'success';
        break;
      default: this.message.style = 'danger';
    }
  }
}



