import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { User } from './user.model';
import { UserSearchResult } from './user-search-result.model';
import { RequestService } from '../../shared/auth/request.service';
import { Exception } from '../../shared/exceptions/exception.model';
import { ExceptionService } from '../../shared/exceptions/exception.service';
import { Affiliation } from 'app/+affiliations/shared/affiliation.model';
import { ManagedUser } from './managed-user.model';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

@Injectable()
export class UsersService {
    private baseUrl = `/portal/api`;
    private updatedUser: BehaviorSubject<UserSearchResult>;

    constructor(private requestService: RequestService, private exceptionService: ExceptionService) {
      this.updatedUser = new BehaviorSubject(new UserSearchResult({}));
    }

    public getCurrentUser(): Observable<Affiliation> {
        return this.requestService.authGet(`${this.baseUrl}/v1/users/current`)
          .map((res) => res.json())
          .catch((error: any) => Observable.throw(this.exceptionService.handleException(error)));
    }

    public getUser(userId: string): Observable<User> {
         return this.requestService.authGet(`${this.baseUrl}/v1/users/${userId}`)
            .map((res) => res.json())
            .catch((error: any) => Observable.throw(this.exceptionService.handleException(error)));
    }

    public getUsers(customerId: string): Observable<UserSearchResult[]> {
         return this.requestService.authGet(`${this.baseUrl}/v1/users?customerId=${customerId}`)
            .map((res) => res.json())
            .catch((error: any) => Observable.throw(this.exceptionService.handleException(error)));
    }

    public postUser(model: ManagedUser): Observable<Affiliation> {
        return this.requestService.authPost(`${this.baseUrl}/v1/users`, model)
            .map((res) => res.json())
            .catch((error: any) => Observable.throw(this.exceptionService.handleException(error)));
    }

    public putUser(model: ManagedUser): Observable<Affiliation> {
        return this.requestService.authPut(`${this.baseUrl}/v1/users/${model.userId}/affiliations/${model.affiliationId}`, model)
            .map((res) => res.json())
            .catch((error: any) => Observable.throw(this.exceptionService.handleException(error)));
    }

    public getUpdatedUser(): Observable<UserSearchResult> {
      return this.updatedUser.share();
    }

    public setUpdatedUser(user: UserSearchResult): void {
      this.updatedUser.next(user);
    }
}



