import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { User } from '../shared/user.model';
import { UnsavedChangesModalComponent } from '../../shared/modal/unsaved-changes-modal.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { MessageService } from '../../shared/message/message.service';
import { ActivatedRoute, Router } from '@angular/router';
import { UsersService } from '../shared/users.service';
import { CustomersService } from '../../+customers/shared/customers.service';
import { Customer } from '../../+customers/shared/customer.model';
import { ManagedUser } from '../shared/managed-user.model';
import { SecurityRole } from '../../shared/security/security-role.model';
import { SecurityRolesService } from '../../shared/security/security-role.service';
import {AppService} from '../../app.service';
import {UserSearchResult} from '../shared/user-search-result.model';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css'],
  providers: [SecurityRolesService]
})

export class UserComponent implements OnInit, OnDestroy {

  @ViewChild('userForm') private userForm: NgForm;
  public customer: Customer = new Customer();

  public title = 'Create';
  public api_model: User = new User();
  public model: ManagedUser = new ManagedUser();
  public securityRoles: Array<SecurityRole> = new Array<SecurityRole>();
  public isRoleEditable: boolean;
  public isActiveEditable = false;
  public fakePassword = '00000000000';
  public defaultPassword = '';
  public showActive = false;

  private currentUser;
  private submittedSuccessfully = false;
  private currentCustomerSubscription: Subscription;
  private authenticatedUserSubscription: Subscription;
  private securityRolesSubscription: Subscription;
  private userSubscription: Subscription;
  private subscriptions = [
    this.currentCustomerSubscription,
    this.authenticatedUserSubscription,
    this.securityRolesSubscription,
    this.userSubscription
  ];

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private messageService: MessageService,
    private usersService: UsersService,
    private customersService: CustomersService,
    private securityRolesService: SecurityRolesService,
    private appService: AppService
  ) {
  }

  public ngOnInit() {
    this.currentCustomerSubscription = this.customersService.getCurrentCustomer().subscribe(customer => {
      this.customer = customer;
    });

    this.authenticatedUserSubscription = this.appService.getAuthenticatedUser().subscribe(currentUser => {
      this.currentUser = currentUser;
    });

    this.securityRolesSubscription = this.securityRolesService.getRoles(this.customer.customerId).subscribe((securityRoles: SecurityRole[]) => {
        this.securityRoles = securityRoles;
        this.setUser();
      },
      (err) => this.handleFailure(err));

  }

  public ngOnDestroy() {
    this.messageService.clearFailure();

    this.subscriptions.forEach(subscription => {
      if (subscription) {
        subscription.unsubscribe();
      }
    });
  }

  private setUser() {
    let id = '';

    this.route.params.subscribe((params) => id = params['id']);

    if (id !== undefined && id !== '') {
      this.title = 'Edit';
      this.showActive = true;
      this.defaultPassword = this.fakePassword;
      this.userSubscription = this.usersService.getUser(id).subscribe((user: User) => {
          this.model = new ManagedUser(user.affiliations[0]);
          this.isActiveEditable = this.canEditActive(user, this.currentUser);
          this.setSecurity();
        },
        (err) => this.handleFailure(err));
    } else {
      this.setSecurity();
      this.model.securityRole.level = null;
      this.model.isActive = true;
    }
  }

  private canEditActive(user, currentUser): boolean {
    if (currentUser && currentUser.securityRole && currentUser.securityRole.level && user && user.affiliations) {
      const userAffiliation = user.affiliations[0];

      if (userAffiliation && userAffiliation.securityRole && userAffiliation.securityRole.level) {
        const isSelf = user.userId === this.currentUser.userId;

        return userAffiliation.securityRole.level >= currentUser.securityRole.level && !isSelf;
      }
    }

    return true;
  }

  private setSecurity() {
    this.isRoleEditable = this.securityRoles.length > 0 &&
      (this.model.securityRole === undefined ||
        this.model.securityRole.level === 0 ||
        this.securityRoles.find(m => m.level === this.model.securityRole.level) !== undefined);
  }

  canDeactivate(): Promise<boolean> | boolean {
    if (this.userForm.dirty && !this.submittedSuccessfully) {
        const result = this.modalService.open(UnsavedChangesModalComponent).result;

        return result.then(
          () => { return true; },
          () => { return false; }
        );

    } else {
      return true;
    }
  }

  public onSubmit() {
    this.model.customerId = this.customer.customerId;
    this.model.username = this.model.email;
    this.model.securityRoleLevel = this.model.securityRole.level;

    /** TODO: This fakepassword implementation is temporary and should be subsumed by a two-field change password process  */
    if (this.userForm.controls.password.value && this.userForm.controls.password.value !== this.fakePassword) {
      this.model.password = this.userForm.controls.password.value;
    }

    const request = this.model.userId ? this.usersService.putUser(this.model) : this.usersService.postUser(this.model);

    request.subscribe(
      (user) => this.handleSuccess(user),
      (err) => this.handleFailure(err)
    );
  }

  private handleFailure(err) {
    this.messageService.fail({ text: err.message });
    this.submittedSuccessfully = false;
  }

  private handleSuccess(user) {
    const userSearchResult = user.affiliations ? new UserSearchResult(user.affiliations[0]) : user as UserSearchResult;
    this.usersService.setUpdatedUser(new UserSearchResult(userSearchResult));
    this.messageService.success({ text: `User ${this.model.username} saved for ${this.customer.name}!`, autoHide: true });
    this.submittedSuccessfully = true;
    this.router.navigate(['/users']);
  }
}



