import { Component, OnDestroy, OnInit } from '@angular/core';
import { Customer } from '../../../+customers/shared/customer.model';
import { Affiliation } from '../../../+affiliations/shared/affiliation.model';
import { PermissionsService } from '../../security/permissions.service';
import { AppService } from '../../../app.service';
import { Permissions } from '../../security/permissions.model';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'sa-navigation',
  templateUrl: './navigation.component.html'
})
export class NavigationComponent implements OnInit, OnDestroy {
  public userInfo: Affiliation;
  public customerInfo: Customer;
  public canIUse: Permissions;
  public userObserver: Observable<Affiliation>;
  public userSubscription: Subscription;
  public permissionSubscription: Subscription;
  private subscriptions: Subscription[] = [
    this.userSubscription,
    this.permissionSubscription
  ];

  public constructor(private appService: AppService, private permissionsService: PermissionsService) {
  }

  public ngOnInit() {
    this.userObserver = this.appService.getAuthenticatedUser();

    this.userSubscription = this.userObserver.subscribe(authenticatedUser => {
      this.userInfo = authenticatedUser;
      this.customerInfo = authenticatedUser.customer;
    });

    this.permissionSubscription = this.permissionsService.getPermissions().subscribe(featureAvailability => {
      this.canIUse = featureAvailability;
    });
  }

  public ngOnDestroy() {
    this.subscriptions.forEach(subscription => {
      if (subscription) {
        subscription.unsubscribe();
      }
    });
  }

}



