import {NgModule} from '@angular/core';
import {NoWhitespaceDirective} from './validators/no-whitespace.directive';
import {CommonModule} from '@angular/common';
import {FormsModule} from '@angular/forms';
import {UnsavedChangesModalComponent} from '../modal/unsaved-changes-modal.component';
import {TrimDirective} from './common/trim.directive';
import {LoadingSpinnerModule} from '../loading-spinner/loading-spinner.module';

// Declares and exposes directives/components/modules common to our forms
@NgModule({
  declarations: [
    NoWhitespaceDirective,
    UnsavedChangesModalComponent,
    TrimDirective
  ],
  imports: [
    CommonModule,
    FormsModule,
    LoadingSpinnerModule
  ],
  exports: [
    CommonModule,
    FormsModule,
    NoWhitespaceDirective,
    UnsavedChangesModalComponent,
    TrimDirective,
    LoadingSpinnerModule
  ],
  providers: [],
})
export class PortalFormsModule {
}



