import { Injectable, EventEmitter } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { Affiliation } from 'app/+affiliations/shared/affiliation.model';
import { AppService } from '../../app.service';

import { OIDCUser } from './oidc/oidc.user.model';
import { OidcSecurityService } from './oidc/services/oidc.security.service';

@Injectable()
export class RequestService {

    public userLoadededEvent: EventEmitter<OIDCUser> = new EventEmitter<OIDCUser>();
    public currentUser: OIDCUser;
    public loggedIn = false;
    public authHeaders: Headers;
    private baseApiUrl = `/portal/api`;

    constructor(private http: Http, private appService: AppService, public oidcSecurityService: OidcSecurityService) {
        this.oidcSecurityService.oidcUserShare
            .subscribe(
                (user: OIDCUser) => {
                    this.currentUser = user;
                    this.userLoadededEvent.emit(user);
                    this.loggedIn = true;
                    this.appService.logDebug('REQUEST SERVICE: OIDCUser Data Loaded', user);
                },
                (error) => {
                    this.appService.logError('REQUEST SERVICE: Error with user authentication', error);
                }, () => {
                    this.loggedIn = true;
                    this.appService.logDebug('REQUEST SERVICE: login completed');
                } );
    }

    public getCurrentSpokUser(): Observable<Affiliation> {
        return this.authGet(`${this.baseApiUrl}/v1/users/current`)
            .map((res) => res.json());
    }

    public authGet(url: string, options?: RequestOptions): Observable<Response> {
        if (this.loggedIn) {
            this.setAuthHeaders(this.oidcSecurityService.getUserData());
        }
        if (options) {
            options = this.setRequestOptions(options);
        } else {
            options = this.setRequestOptions();
        }
        return this.http.get(url, options);
    }

    public authPut(url: string, data: any, options?: RequestOptions): Observable<Response> {
        if (this.loggedIn) {
            this.setAuthHeaders(this.oidcSecurityService.getUserData());
        }
        const body = JSON.stringify(data);

        if (options) {
            options = this.setRequestOptions(options);
        } else {
            options = this.setRequestOptions();
        }
        return this.http.put(url, body, options);
    }

    public authDelete(url: string, options?: RequestOptions): Observable<Response> {
        if (this.loggedIn) {
            this.setAuthHeaders(this.oidcSecurityService.getUserData());
        }
        if (options) {
            options = this.setRequestOptions(options);
        } else {
            options = this.setRequestOptions();
        }
        return this.http.delete(url, options);
    }

    public authPost(url: string, data: any, options?: RequestOptions): Observable<Response> {
        if (this.loggedIn) {
            this.setAuthHeaders(this.oidcSecurityService.getUserData());
        }
        const body = JSON.stringify(data);

        if (options) {
            options = this.setRequestOptions(options);
        } else {
            options = this.setRequestOptions();
        }
        return this.http.post(url, body, options);
    }

  private setAuthHeaders(user: any) {
    this.authHeaders = new Headers();
    this.authHeaders.append('Authorization', user.token_type + ' ' + user.access_token);
    this.authHeaders.append('Content-Type', 'application/json');
    this.authHeaders.append('Cache-Control', 'no-cache');
    this.authHeaders.append('Pragma', 'no-cache');
  }

  private setRequestOptions(options?: RequestOptions) {
    const headerList = this.authHeaders;

    if (options) {
      options.headers.keys().forEach(key => {
        const value = options.headers.get(key);
        if (value) {
          headerList.append(key, value);
        }
      });
    }
    options = new RequestOptions({ headers: headerList });
    return options;
  }

}



