import { Component, ViewChild, ReflectiveInjector, ComponentFactoryResolver } from '@angular/core';
import { AppService } from '../../app.service';
import { NgbModal, NgbActiveModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { Idle, DEFAULT_INTERRUPTSOURCES } from '@ng-idle/core';
import { IdleWarningModalComponent } from './idle-warning.component';

export class TimeOutConfig {
  public idle_timeout = 600;
  public idle_countdown = 45;
}

@Component({
  selector: 'app-idle',
  template: ``
})
export class IdleComponent {

  idleState = 'Not started.';
  timedOut = false;
  myModal: NgbModalRef;
  timeOutConfig: TimeOutConfig;

  constructor(private appService: AppService, private idle: Idle, private modal: NgbModal) {
    this.timeOutConfig = new TimeOutConfig();
    idle.setIdle(this.timeOutConfig.idle_timeout);
    idle.setTimeout(this.timeOutConfig.idle_countdown);
    idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);

    idle.onIdleEnd.subscribe(() => {
      this.idleState = 'Click anywhere to remain logged in.';
      this.setModalMessage(this.idleState);
    });
    idle.onTimeout.subscribe(() => {
      this.idleState = 'Automatically logging out now.';
      this.timedOut = true;
      this.setModalMessage(this.idleState);
      this.appService.logoff();
    });
    idle.onIdleStart.subscribe(() => {
      this.idleState = 'You have gone idle';
      if (this.myModal) {
        this.myModal.close();
        this.myModal = null;
      }
      this.myModal = modal.open(IdleWarningModalComponent);
      this.myModal.result.then((res) => {
        this.reset();
      }, (error) => {
      });
    });

    idle.onTimeoutWarning.subscribe((countdown) => {
      this.setModalMessage(null, countdown);
    });

    this.reset();
  }

  reset(): void {
    this.idle.watch();
    this.idleState = 'Idle watch started';
    this.timedOut = false;
    if (this.myModal) { this.myModal.close(); }
  }

  setModalMessage(message: string, timeout?: number) {
    if (this.myModal && this.myModal.componentInstance) {
      this.myModal.componentInstance.setMessage(message);
      if (!timeout) {
        this.myModal.componentInstance.setTimeOutValue(-1);
      } else {
        this.myModal.componentInstance.setTimeOutValue(timeout);
      }
    }
  }

}



